html, body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  font: {
    family: "Fira Sans", sans-serif;
    weight: 400;
    size: 15px;
  }
  color: $black;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent;
}

*, ::before, ::after {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
}

input {
  outline: none;
  border: none;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}
